export default {
    data(){
        return {
            hottalkList:[]
        }
    },
    onShow(){
        this.gethottalk()
    },
    methods: {
        gethottalk(){
            this.$http.gethottalk().then(res=>{
                if(res.status === 200) {
                    this.hottalkList = res.data.data
                }
            })
        }
    },
}
